import {
  AlipayOutlined,
  CrownTwoTone,
  Html5TwoTone,
  ZhihuOutlined,
} from '@ant-design/icons'
import {
  AdProduct,
  ApiApp,
  Help,
  RightSmall,
  StereoNesting,
  Tiktok,
  Wechat,
  Xigua,
} from '@icon-park/react'
import { Avatar, Button, Progress, Table, Timeline } from 'antd'
import classNames from 'classnames'
import { useEffect, useState } from 'react'

import { getDashboardTableData } from '@/api/common'
import profileImg from '@/assets/profile_img.png'
import { useTypedSelector } from '@/redux'

import RadislBarChart from './charts/RadialBarChart'
import StackedColumnChart from './charts/StackedColumnChart'
import Row from './components/Row'

export default function DashboardDefault() {
  const nickname = useTypedSelector(({ user }) => user.info.nickname)
  const avatar = useTypedSelector(({ user }) => user.info.avatar)
  const tableColumn = [
    {
      title: ' ID',
      dataIndex: 'id',
      render(id) {
        return <span className="font-semibold">#{id}</span>
      },
    },
    {
      title: '',
      dataIndex: 'date',
    },
    {
      title: '',
      dataIndex: 'name',
    },
    {
      title: '',
      dataIndex: 'total',
      render(total) {
        return <span>{total}</span>
      },
    },
    {
      title: '',
      dataIndex: 'status',
      render(status) {
        const statusClass = ((state) => {
          switch (state) {
            case 1:
              return 'success bg-success-light'
            case 0:
              return 'danger bg-danger-light'
            default:
              return 'warning bg-warning-light'
          }
        })(status)
        return (
          <span className={`px-2 py-1 rounded-lg ${statusClass}`}>
            {status === 1 ? '' : ''}
          </span>
        )
      },
    },
    {
      title: '',
      dataIndex: 'payment',
      render(payment) {
        const Icon = payment === '' ? ZhihuOutlined : AlipayOutlined
        return (
          <div className="flex items-center">
            <Icon className="mr-1" />
            {payment}
          </div>
        )
      },
    },
    {
      title: '',
      render() {
        return <Button type="primary"></Button>
      },
    },
  ]
  const [tableLoading, setTableLoading] = useState(false)
  const [tableData, setTableData] = useState([])
  const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([])

  useEffect(() => {
    void (async function getTableData() {
      try {
        setTableLoading(true)
        const {
          data: { list },
        } = await getDashboardTableData({})
        setTableData(list)
      } finally {
        setTableLoading(false)
      }
    })()
  }, [])

  return (
    <>
      <Row>
        <div className="flex flex-col w-1/3 pr-3">
          <div className="mb-6 overflow-hidden bg-white rounded-md">
            <div
              className="relative p-4 primary"
              style={{ background: 'rgba(var(--primary), .25)' }}
            >
              <p className="mb-2 font-bold">{`${
                nickname ?? ''
              }`}</p>
              <p className="mb-6 text-xs">Portal </p>
              <img
                alt="profile"
                className="absolute bottom-0 right-0 w-2/5"
                src={profileImg}
              />
            </div>
            <div className="flex">
              <div className="relative px-4">
                <Avatar
                  className="relative"
                  size={62}
                  src={avatar}
                  style={{ bottom: '25px', boxShadow: '0 0 0 5px #fff' }}
                />
                <div className="truncate"></div>
                <div className="mt-2 text-sm truncate secondary">
                  
                </div>
              </div>
              <div className="w-2/3 p-5 ml-auto">
                <div className="flex mb-6">
                  <div className="flex-1">
                    <div className="mb-2 font-semibold">125</div>
                    <div className="text-xs secondary"> </div>
                  </div>
                  <div className="flex-1">
                    <div className="mb-2 font-semibold">1245</div>
                    <div className="text-xs secondary"> </div>
                  </div>
                </div>
                <Button type="primary"></Button>
              </div>
            </div>
          </div>
          <div className="custom-card">
            <h4 className="custom-card__title"></h4>
            <div className="flex items-center">
              <div>
                <div className="text-sm text-gray-500"></div>
                <div className="mt-3 mb-1 text-xl font-bold">34,252</div>
                <p className="mb-5 text-sm">
                  &nbsp;
                  <span className="success"> 12%</span>
                </p>
                <Button type="primary">
                  <div className="flex items-center">
                    
                    <RightSmall size={18} style={{ margin: '0 0 2px 4px' }} />
                  </div>
                </Button>
              </div>
              <div className="ml-auto" style={{ height: '180px' }}>
                <RadislBarChart />
              </div>
            </div>
            <div className="flex items-center mt-3 text-sm text-gray-500">
              <Help className="mr-1" size={18} />
              ...
            </div>
          </div>
        </div>

        <div className="w-2/3 pl-3">
          <div className="flex mb-6">
            {[
              { label: '', value: '1,235', icon: AdProduct },
              { label: '', value: '1,235', icon: StereoNesting },
              { label: '', value: '23.5', icon: ApiApp },
            ].map(({ label, value, icon: Icon }, i) => (
              <div
                key={label}
                className={classNames(
                  'custom-card w-1/3 flex justify-between items-center',
                  { 'ml-6': i >= 1 }
                )}
              >
                <div>
                  <div className="mb-3 secondary">{label}</div>
                  <div className="text-xl font-semibold">{value}</div>
                </div>
                <div className="flex items-center justify-center w-10 h-10 rounded-full bg-primary">
                  <Icon fill="#fff" size={22} />
                </div>
              </div>
            ))}
          </div>
          <div className="custom-card">
            <div className="flex items-center justify-between">
              <h4 className="custom-card__title"></h4>
              <div className="flex items-center text-sm">
                <div className="cursor-pointer"></div>
                <div className="mx-6 cursor-pointer"></div>
                <div className="px-3 py-1 text-white rounded cursor-pointer bg-primary">
                  
                </div>
              </div>
            </div>
            <StackedColumnChart />
          </div>
        </div>
      </Row>

      <Row>
        <div className="w-1/3 pr-3">
          <div className="custom-card">
            <h4 className="custom-card__title"></h4>
            <div className="mb-6 text-center">
              <Html5TwoTone className="mt-6 text-4xl" twoToneColor="#556ee6" />
              <h3 className="my-3"> -  625</h3>
              <p className="text-sm text-gray-500">
                antd  Ant Design  React UI
                
              </p>
              <div className="mt-3 text-sm cursor-pointer primary">
                 &gt;
              </div>
            </div>
            <div className="flex justify-around">
              {[
                { label: '', icon: Wechat, count: 124 },
                { label: '', icon: Tiktok, count: 124 },
                { label: '', icon: Xigua, count: 124 },
              ].map(({ label, icon: Icon, count }) => (
                <div key={label} className="text-center">
                  <Icon size={22} />
                  <div className="my-1">{label}</div>
                  <div className="text-sm text-gray-500"> {count}</div>
                </div>
              ))}
            </div>
          </div>
        </div>

        <div className="w-1/3 px-3">
          <div className="custom-card">
            <h4 className="custom-card__title"></h4>
            <Timeline className="mt-12" pending="...">
              <Timeline.Item>
                <span className="mr-3 font-semibold">2020-06-01</span>
                
              </Timeline.Item>
              <Timeline.Item>
                <span className="mr-3 font-semibold">2020-06-04</span>
                
              </Timeline.Item>
              <Timeline.Item>
                <span className="mr-3 font-semibold">2020-06-05</span>
                
              </Timeline.Item>
              <Timeline.Item>
                <span className="mr-3 font-semibold">2020-09-01</span>
                
              </Timeline.Item>
            </Timeline>
          </div>
        </div>

        <div className="w-1/3 pl-3">
          <div className="custom-card">
            <h4 className="custom-card__title"></h4>
            <div className="mb-8 text-center">
              <CrownTwoTone
                className="mt-6 mb-4 text-4xl"
                twoToneColor="#556ee6"
              />
              <p className="text-2xl font-semibold">1,696</p>
              <div className="mt-1 text-sm secondary"></div>
            </div>
            <div>
              {[
                { city: '', value: 1696, color: 'primary' },
                { city: '', value: 1599, color: 'success' },
                { city: '', value: 1298, color: 'warning' },
              ].map(({ city, value, color }) => (
                <div
                  key={city}
                  className="flex items-center justify-between py-3"
                  style={{ borderTop: '1px solid #eff2f7' }}
                >
                  <div className="w-1/3 truncate">{city}</div>
                  <div className="ml-2 mr-4 font-bold">
                    {value.toLocaleString()}
                  </div>
                  <Progress
                    className="flex-1"
                    percent={(value / 2000) * 100}
                    showInfo={false}
                    strokeColor={`rgba(var(--${color}, 1)`}
                    strokeWidth={5}
                  />
                </div>
              ))}
            </div>
          </div>
        </div>
      </Row>

      <Row>
        <div className="w-full custom-card">
          <h4 className="custom-card__title"></h4>
          <Table
            columns={tableColumn}
            dataSource={tableData}
            loading={tableLoading}
            rowKey="id"
            rowSelection={{
              selectedRowKeys,
              onChange: (keys) => {
                setSelectedRowKeys(keys)
              },
            }}
          />
        </div>
      </Row>
    </>
  )
}
